input {
  display: block;
  width: 100%;
  padding: 8px;
  box-sizing: border-box;
  border: 1px solid #ddd;
  border-radius: 10px;
  font-size:medium;
  background-color:wheat;
}

select {
  display: block;
  width: 100%;
  margin-bottom: 15px;
  padding: 10px;
  box-sizing: border-box;
  border: 1px solid #ddd;
  border-radius: 10px;
}

label {
  font-size: 15px;
  display: block;
  width: 100%;
  margin-top: 8px;
  margin-bottom: 5px;
  text-align: left;
  color:black;
  font-weight: bold;
  border-radius: 20px;
}

button {
  padding: 15px;
  border-radius: 10px;
  margin: 15px;
  border: none;
  color:white;
  cursor: pointer;
  background-color: #129793;
  width: 45%;
  font-size: 16px;
}
textarea {
  resize: none;
  width: 75%;
  min-height: 100px;
  max-height: 150px;
border-radius: 10px;

}
